@import '../../global.scss';

.home{
    position: relative;
    
    .mobile-btn{
        display: none;
        width: 20%;
        position: fixed;
        bottom: 10px;
        right: 6vw;
        padding: 10px;
        font-size: 30px;
        background-color: white;
        text-align: center;
        justify-content: center;
        align-items: center;
        border-radius: 50px;
        cursor: pointer;
        
        
        @include tablet{
            display: flex;
        }
    }

    .title{
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;

        @include tablet{
            display: none;
        }
    }

    .base-color{
        z-index: 9;
        position: sticky;
        position: -webkit-sticky;
        top:0px;
        height: 90px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        transition: all .5s ease-in-out;
        
        @include tablet{
        top:0px;

        }

        .selectors{

            @include mobile{
                width: 100px;
            }
        }
    }

}